.wrapper {  
  margin: 2em auto;  
  font-family: "Michroma", sans-serif;
  width: 100%;
  display: flex;
  justify-content: center;
}
.hero {
  height: 12rem;
  width: 16rem;  
  box-shadow: 0em 0em 0.5em 0.02em rgba(0, 0, 0, 4);
  background: #1d1f20;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__text {
  display: block;
  opacity: 0;
  transform: translateY(0.75em);
  font-size: 1.3em;
  font-style: italic;
  font-weight: bold;
  color: #fff;
  letter-spacing: 0.05em;
  transition: all 750ms ease;
}
.hero__overlay {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero__overlay::after,
.hero__overlay::before {
  content: "";
  border-color: #fff;
  transition: all 600ms;
  border-style: solid;
  border-width: 0;
  height: 1.5em;
  position: absolute;
  width: 1.5em;
  opacity: 0;
}
.hero__overlay::before {
  border-right-width: 0.125em;
  border-top-width: 0.125em;
  right: -0.3125em;
  top: -0.3125em;
}
.hero__overlay::after {
  border-bottom-width: 0.125em;
  border-left-width: 0.125em;
  bottom: -0.3125em;
  left: -0.3125em;
}
.hero__overlay:hover::before,
.hero__overlay:hover::after {
  width: 14.1em;
  height: 10.2em;
  opacity: 1;
}
.hero__overlay:hover .hero__text {
  opacity: 1;
  transform: translateY(0em);
}

